import React, {Component} from 'react';
import SearchResults from './SearchResults/SearchResults';
import Input from '../../components/UI/Input/Input';
import Button from '../../components/UI/Button/Button';
import classes from './Search.css';
import Spinner from '../../components/UI/Spinner/Spinner';
import Modal from '../../components/UI/Modal/Modal';
import axios from '../../axios-search';
import Aux from '../../hoc/Auxiliary';
import PageSwap from '../../components/UI/PageSwap/PageSwap';

class Search extends Component{
    
    state = {
        show: false,
        orderForm: {
            Search: {
                elementType: 'input',
                elementConfig: {
                    name: 'search',
                    type: 'text',
                    placeholder: 'Enter Text to search'
                },
                value: '',
                validation: {
                    required: true
                },
                valid:false,
                touched:false
            }
        },
            loading:false,
            error:null,
            data:null,
            length:0,
            page:0,
            alias:"false"
    }

    checkValidity(value,rules){
        let isValid = true;
        if (!rules) {
            return true;
        }
        
        if (rules.required) {
            isValid = value.trim() !== '' && isValid;
        }

        return isValid;
    }

    closeHandler=()=>{
        this.setState({
            show: false,
            error: null
        });
    }

    inputChangedHandler = (event, inputIdent) => {
        const updatedOrderForm = {
            ...this.state.orderForm,
            [inputIdent] : {
                ...this.state.orderForm[inputIdent],
                value: event.target.value,
                valid: this.checkValidity(event.target.value, this.state.orderForm[inputIdent].validation),
                touched: true
            }
        };
        this.setState({orderForm:updatedOrderForm});
    }

    searchQuery=(event)=>{
        event.preventDefault();
        this.setState({loading: true});
        axios.get('/v60/admin/search/user?keyword='+ this.state.orderForm['Search'].value +'&alias'+this.state.alias)
             .then(response=>{
                 this.setState({loading: false});
                 if(!response.data){
                    this.setState({
                        show:true,
                        error: "No results found, please retry",
                        loading: false,
                        length:0
                    });
                 }else{
                    let values = Object.values(response.data);
                    let final = [];
                    let counter = 0;
                    let portion = {};
        
                    for (var key in response.data){
                        if (counter !== 0 && counter % 5 ===0){
                            final.push(portion);
                            portion= {};
                        }
                        portion[key] = values[counter];
                        counter++;
                    }
                    final.push(portion);
                    
                     this.setState({
                         data: final,
                         length: Math.ceil(response.data.length/5)
                     });
                     this.props.history.push({pathname:'/search/1'}); 
                 }
             }).catch(except=>{
                this.setState({
                    show: true,
                    error: except,
                    loading: false
                 });
             });
    }

    pageChange = (id) =>{
        this.props.history.push({pathname:'/search/'+id});
        //this.setState((prevProps, prevState)=>{return {page:prevProps.page+inc}});
    }

    render(){

        if(this.props.match.params.id && this.props.match.params.id!==this.state.page){
            this.setState({
              page: this.props.match.params.id
            })
        }

        const formElementArray = [];
        for (let key in this.state.orderForm){
            formElementArray.push({
                id:key,
                config: this.state.orderForm[key]
            });
        }
        
        let form = (
            <form onSubmit={this.searchQuery}>
                {formElementArray.map(formElement=>{
                    return(
                        <Input key={formElement.id}
                               elementType={formElement.config.elementType}
                               elementConfig={formElement.config.elementConfig}
                               value={formElement.config.value}
                               invalid={!formElement.config.valid}
                               shouldValidate={formElement.config.validation}
                               touched={formElement.config.touched}
                               //label={formElement.id}
                               changed={(event)=>this.inputChangedHandler(event,formElement.id)}/>
                               
                    );
                })}
                <Button btnType="Success">SEARCH</Button>
            </form>
        );
        if (this.state.loading) {
            form=<Spinner/>;
        }
        let data=null;
        if (this.state.data && this.state.page>0 && this.state.page<=this.state.length){
            //console.log(this.state.data[this.state.page-1]);
            data=<Aux>
                    <SearchResults data={this.state.data[this.state.page-1]} len={this.state.length}>
                        <PageSwap id={this.state.page} change={(id)=>this.pageChange(id)} length={this.state.length}/>
                    </SearchResults>
                 </Aux>
        }
        return(
            <Aux>
                <div className={classes.Search}>
                    <Modal show={this.state.show} modalClosed={this.closeHandler}>
                        {this.state.error}
                    </Modal>
                    {form}
                </div>
                {data}
            </Aux>
        );
    }
}

export default Search;


