import axios from 'axios';

const instance = axios.create({
    baseURL: 'http://3.122.7.162:5000'
});

instance.defaults.headers.common['set-cookie']=localStorage.getItem('set-cookie');

instance.interceptors.request.use(request=>{
    return request;
},error=>{
    console.log(error);
    return Promise.reject(error);
});

instance.interceptors.response.use(response=>{
    return response;
},error=>{
    console.log(error);
    return Promise.reject(error);
});


//instance.

export default instance;


