import React from 'react';
import Button from '../Button/Button';
import Aux from '../../../hoc/Auxiliary';

const pageSwap = props =>{
    let prev=null;
    let next=null;
    //console.log(props.length);
    if(props.id>1){
        prev=<Button btnType="Success" clicked={()=>props.change(props.id-1)}>Prev </Button>
    }
    if(props.id<props.length){
        next=<Button btnType="Success" clicked={()=>props.change(1+parseInt(props.id))}> Next</Button>
    }
    return(
        <Aux>
            {prev}

            {" "+props.id+" "}
            
            {next}
        </Aux>
    );
}

export default pageSwap;


