import React from 'react';

const row = (props)=>{
    let rowEntry = [];

    let stl=null;
    if(+props.data.attributes[0].value<50){
        stl = {color:"red"};
    }
    

    rowEntry.push(<td key={props.id+"id"}>{props.data.id}</td>);
    rowEntry.push(<td key={props.id+"displayName"}>{props.data.displayName}</td>);
    rowEntry.push(<td key={props.id+"status"}>{props.data.status}</td>);
    return(
            <tr key={props.id} style={stl}>
                {rowEntry}
            </tr>
        );
}

export default row;


