import React, {Component} from 'react';
import axios from '../../../axios-search';
import classes from './SearchResults.css';
import Row from '../../../components/UI/Rows/Rows';

class SearchResults extends Component{

    result(){
        //event.preventDefault();
        axios.get('/v60/admin/search/user?keyword=test&alias=false')
             .then(response=>{
                 //console.log(response);
             });
    }

    render(){
        let row=[];
        let table_headers=[];
        let data= Object.values(this.props.data).map((value)=>{
            return value
        });
        let headers = Object.keys(data[0]);
        headers = headers.splice(0,3);
        //console.log(headers);
        headers.forEach(header => table_headers.push(<th key={header}>{header}</th>));
        data.forEach(entry=> row.push(<Row key={entry.id} id={entry.id} data={entry} headers={headers}/>))
        return(
            <div style={{textAlign:"center",
                    paddingLeft:"30px"}}>
                {this.props.children}
                <table className={classes.greenTable}>
                    <thead>
                        <tr>
                            {table_headers}
                        </tr>
                    </thead>
                    <tbody>
                            {row}
                    </tbody>
                </table>
            </div> 
        );
    }
}

export default SearchResults;


